html, body {
  margin: 0px;
  padding: 0px;
  font-family: "Source Sans Pro", "Hiragino Sans GB", "Microsoft Yahei", SimSun, Helvetica, Arial, Sans-serif, monospace;
  width: 100%;
  height: 100%;
}

/* Designing for scroll-bar */

::-webkit-scrollbar {
  width: 8px;
}

/* Track */

::-webkit-scrollbar-track {
  background: #191919;
  border-radius: 4px;
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: #000;
  border-radius: 4px;
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: #212121;
}

a {
  text-transform: none;
  text-decoration: none;
  color: inherit;
}

li, ul, ol {
  -webkit-padding-start: 0%;
          padding-inline-start: 0%;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  list-style: none;
}

hr {
  border: 1px solid #252525;
}

html, #centent {
  color: #9daaaa;
  background-color: #191919;
}

#aside, #header, #content > aside, .article, ul {
  color: #aaaaaa;
  background-color: #212121;
}

/* #aside-bottom {

} */

#header {
  color: #56666e;
}

#footer, #aside-bottom {
  background-color: #252525;
  color: #aaaaaa;
}

#footer a:hover {
  color: white
}

#articles .article-abstract {
  color: #777777;
}

li:hover, .button:hover {
  background-color: #1a1a1a;
}

div.checked {
  background-color: #191919;
}

div.unchecked {
  background-color: white;
}

.tags {
  border-radius: 10px;
  padding: 3px 7px;
  color: #8f8f8f;
  font-weight: bold;
  font-size: 12px;
  background-color: black;
}

.page-list-item {
  font-size: 12px;
  width: 20px;
  height: 20px;
  display: inline;
  padding: 8px;
  border-radius: 0px;
  background-color: #212121;
}

.page-list-item:first-child {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

.page-list-item:last-child {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.svg-icon {
